.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;
}
.pace-inactive {
  display: none;
}
.pace .pace-progress {
  background: #afafb0;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 2px;
}
.pace .pace-progress-inner {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #afafb0, 0 0 5px #afafb0;
  opacity: 1;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}
.pace .pace-activity {
  display: block;
  position: fixed;
  z-index: 2000;
  top: 15px;
  right: 15px;
  width: 25px;
  height: 25px;
  border: solid 2px transparent;
  border-top-color: #afafb0;
  border-left-color: #afafb0;
  border-radius: 10px;
  -webkit-animation: pace-spinner 400ms linear infinite;
  animation: pace-spinner 400ms linear infinite;
}
@-webkit-keyframes pace-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes pace-spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
* {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  overflow-y: hidden;
}
.fl {
  float: left!important;
}
.fr {
  float: right!important;
}
.clearfix {
  display: block;
}
.clearfix::after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  font-size: 0;
}
.main-content {
  height: 100%;
  display: flex;
  overflow-x: auto;
}
.main-content .left {
  height: 100VH;
  background: #000c17;
}
.main-content .right {
  flex: 1 1;
  height: 100VH;
  width: 100%;
  position: relative;
  overflow-y: hidden;
}
#adminstock-app {
  height: 100%;
  overflow-y: hidden;
  min-width: 1200px;
}
.sider-bar-wrapper {
  position: relative;
  z-index: 2;
  height: 100vh;
}
.sider-bar-wrapper.sider-bar-open {
  width: 260px;
}
.sider-bar-wrapper.sider-bar-close {
  width: 80px;
}
.sider-bar-wrapper .ant-menu-inline {
  width: 260px;
}
.sider-bar-wrapper .collapsed-sidebar {
  position: absolute;
  right: -65px;
  z-index: 2;
  top: 14px;
}
.sider-bar-wrapper .logo {
  height: 60px;
  text-align: center;
  line-height: 60px;
  background: #00274E;
  font-size: 26px;
  font-weight: bolder;
  cursor: pointer;
}
.sider-bar-wrapper .logo a {
  color: #FFF;
  background: inherit;
}
.sider-bar-wrapper .logo i {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.sider-bar-wrapper .logo span {
  display: inline-block;
  margin-left: 10px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  opacity: 1;
}
.sider-bar-wrapper .logo span.hidden {
  width: 0;
  font-size: 12px;
  opacity: 0;
  margin-left: 0;
  position: absolute;
}
.sider-bar-wrapper .menu-box {
  position: absolute;
  top: 60px;
  bottom: 0;
  overflow: auto;
  overflow-x: hidden;
  left: 0;
  right: 0;
}
.sider-bar-wrapper .menu-box::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}
.sider-bar-wrapper .menu-box::-webkit-scrollbar {
  width: 3px;
  background-color: #F5F5F5;
}
.sider-bar-wrapper .menu-box::-webkit-scrollbar-thumb {
  background-color: #000000;
  border: 2px solid #555555;
}
.nav-bar-wrapper {
  height: 60px;
  padding-left: 88px;
  padding-right: 30px;
  line-height: 60px;
  border-radius: 0!important;
  border-bottom: 1px solid #e6e6e6;
}
.nav-bar-wrapper .text {
  color: #333;
  font-size: 16px;
  font-weight: bolder;
  line-height: 1;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}
.nav-bar-wrapper .site-menu {
  display: inline-block;
  vertical-align: middle;
}
.main-wrapper {
  position: absolute;
  top: 60px;
  bottom: 70px;
  left: 0;
  right: 0;
  background: #f0f2f5;
  padding: 15px 20px;
}
.main-wrapper .main-body {
  margin-top: 10px;
  background: #FFF;
  padding: 15px;
}
.footer-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 70px;
}
.cursor-pointer {
  cursor: pointer;
}
